const translates = {
    'zh-CN': {
        "Change node's attributes and save.": "",
        "The same process for date.":"date",
        "Ctrl + Mouse Left Button.": "Ctrl + ",
        "Make a preview.": "",
        "Save all rules.": "",
    }
};
const t = (text) => {
    const lang = localStorage.getItem('lang');
    if (translates[lang] && translates[lang][text]) return translates[lang][text];
    return text;
};

export {t}
